import { Sprite, Texture } from "pixi.js";
import { Color } from "./Color";
import { Component } from "./Component";

export interface SceneRoot {
  canvas: Component;
  parent: HTMLElement;
}

export interface Scene {
  mount(root: SceneRoot): void;

  toggleVisibility(ok: boolean): void;

  run(): Promise<any>;
}

export interface LoadingAnimation extends Scene {
  play<T>(payload: () => Promise<T>): Promise<T>;
}

interface IPixiSceneProps {
  bgTint: Color;
  bgAlpha: number;
}

export class PixiScene extends Component<IPixiSceneProps> implements Scene {
  protected readonly bg: Sprite;

  constructor(props: Partial<IPixiSceneProps> = {}) {
    super(
      Object.assign(
        {},
        {
          bgTint: Color.fromHex(0xffffff),
          bgAlpha: 1,
        },
        props
      )
    );

    this.bg = this.addSprite({
      texture: Texture.WHITE,
      zIndex: -1e5,
    });
    setTimeout(() => {
      this.bg.width = this.layout.width;
      this.bg.height = this.layout.height;
    }, 0);

    this.props.bgTint.onUpdate((v) => (this.bg.tint = v.i32));
    this.props.bgAlpha.onUpdate((v) => (this.bg.alpha = v));
  }

  mount(root: SceneRoot) {
    const { canvas } = root;
    this.position.set(0);
    this.scaleToFit(canvas.layout.width, canvas.layout.height);
    canvas.addChild(this);
  }

  toggleVisibility(ok: boolean) {
    this.ui.visible = ok;
  }

  async run() {}
}

export class HtmlScene implements Scene {
  protected readonly root: HTMLElement;

  constructor() {
    this.root = document.createElement("div");
    this.root.classList.add("fill-parent");
  }

  mount(root: SceneRoot) {
    const { parent } = root;
    parent.appendChild(this.root);
  }

  toggleVisibility(ok: boolean) {
    if (ok) {
      this.root.classList.add("hidden");
    } else {
      this.root.classList.remove("hidden");
    }
  }

  async run() {}
}
