import { Component } from "./pixi/Component";
import { CompositeTilemap } from "@pixi/tilemap";
import { Texture } from "pixi.js";
import { App } from "./pixi/App";

type TextureSpec = Texture | string;
type TileSpec =
  | TextureSpec
  | {
      texture: TextureSpec;
      alpha: number;
    };

interface IGridComponentProps<T> {
  tileset: Map<T, TileSpec>;
  matrix: Matrix<T>;
  transform: {
    dx?: number;
    dy?: number;
    anchor?: number | Coordinate;
    scale?: number | Coordinate;
    alpha?: number;
  };
}

export class GridComponent<T> extends Component<IGridComponentProps<T>> {
  layout = {
    width: 0,
    height: 0,
  };

  constructor() {
    super({
      tileset: new Map(),
      matrix: {
        width: 0,
        height: 0,
        spaces: [],
      },
      transform: {},
    });

    const root = this.addContainer();
    const tilemap = new CompositeTilemap();
    root.addChild(tilemap);

    const fn = () => {
      const matrix = this.props.matrix.value;
      const transform = this.props.transform.value;
      const tileset = this.props.tileset.value;

      tilemap.clear();
      if (matrix == null) {
        return;
      }
      if (tileset.size == 0) {
        return;
      }

      let texture = tileset.values().next().value;
      if (typeof texture == "object" && !(texture instanceof Texture)) {
        texture = texture.texture;
      }
      if (typeof texture == "string") {
        texture = App.texture(texture);
      }
      const { width, height } = texture;

      const { spaces: li, width: w, height: h } = matrix;
      let {
        dx = width,
        dy = height,
        anchor = 0,
        scale = 1,
        alpha = 1,
      } = transform;

      let [sx, sy] =
        typeof scale == "number" ? [scale, scale] : [scale.x, scale.y];
      root.scale.set(sx, sy);

      let [ax, ay] =
        typeof anchor == "number" ? [anchor, anchor] : [anchor.x, anchor.y];
      [ax, ay] = [width * ax, height * ay];

      [dx, dy] = [dx / sx, dy / sy];

      for (let y = 0; y < h; ++y) {
        for (let x = 0; x < w; ++x) {
          const key = li[x + y * w];
          let spec = tileset.get(key);
          if (spec == null) {
            continue;
          }
          if (spec instanceof Texture || typeof spec == "string") {
            spec = {
              texture: spec,
              alpha: 1,
            };
          }
          tilemap.tile(spec.texture, x * dx - ax, y * dy - ay, {
            tileWidth: width,
            tileHeight: height,
            alpha: spec.alpha * alpha,
          });
        }
      }
    };
    this.props.matrix.onUpdate(fn);
    this.props.transform.onUpdate(fn);
    this.props.tileset.onUpdate(fn);
  }
}
