import { Card } from "../../core/Card";
import { CardGridComponent } from "./CardGridComponent";
import { Component } from "./pixi/Component";
import { Color } from "./pixi/Color";
import { GridComponent } from "./GridComponent";
import { App } from "./pixi/App";
import { CardInteractions } from "./CardInteractions";

interface ISmallCardComponentProps {
  card: Card;
  spFire: boolean;
}

export class SmallCardComponent extends Component<ISmallCardComponentProps> {
  readonly interactions: CardInteractions;

  layout = {
    width: 153 * 2,
    height: 196 * 2,
    radius: 7 * 2,
    padding: 9 * 2,
    boardScale: 0.934,
    bgColor: 0x4f5055,
    szMeter: {
      margin: 6 * 2,
      radius: 11 * 2,
      width: 48 * 2,
      height: 44 * 2,
    },
    spMeter: {
      x: 58 * 2,
      y: 153 * 2,
      width: 13 * 2,
      padding: 1.5 * 2,
    },
  };

  constructor() {
    super({
      card: Card.fromId(1),
      spFire: false,
    });

    const {
      width,
      height,
      radius,
      padding,
      boardScale,
      bgColor,
      spMeter: spMeterLayout,
      szMeter: szMeterLayout,
    } = this.layout;

    const root = this.addContainer();

    const cardRoot = this.addContainer({ parent: root });
    this.lock(cardRoot);

    const bg = this.addGraphics({ parent: cardRoot })
      .beginFill(bgColor)
      .drawRoundedRect(0, 0, width, height, radius);

    const img = this.addSprite({
      parent: cardRoot,
      x: 0,
      y: padding,
      width,
      height,
      tint: Color.fromHex(0xafafaf),
    });

    const grid = this.addComponent(new CardGridComponent(), {
      parent: cardRoot,
      x: padding,
      y: padding,
      scale: { width: width - 2 * padding, height: width - 2 * padding },
    });
    grid.update({ flat: true });
    grid.scale.y = boardScale;

    this.addGraphics({ parent: cardRoot })
      .beginFill(Color.BLACK.i32)
      .drawRoundedRect(
        szMeterLayout.margin,
        height - szMeterLayout.margin - szMeterLayout.height,
        szMeterLayout.width,
        szMeterLayout.height,
        szMeterLayout.radius
      );

    const szMeter = this.addText({
      parent: cardRoot,
      anchor: 0.5,
      x: szMeterLayout.margin + szMeterLayout.width / 2,
      y: height - szMeterLayout.margin - szMeterLayout.height / 2,
      style: {
        fill: 0xeeeeee,
        fontFamily: "Splatoon",
        fontSize: szMeterLayout.height * 0.7,
      },
    });

    const spMeterRoot = this.addContainer({
      parent: cardRoot,
      x: spMeterLayout.x,
      y: spMeterLayout.y,
    });
    const sxy = spMeterLayout.width / 40;
    spMeterRoot.scale.set(sxy);
    const dx = 40 + spMeterLayout.padding / sxy;
    const spMeter = this.addComponent(new GridComponent(), {
      parent: spMeterRoot,
    });
    spMeter.update({
      tileset: new Map([[true, "pure_orange.png"]]),
      transform: {
        // anchor: 0.5,
        dx,
        dy: dx,
      },
    });

    const fn = () => {
      const card = this.props.card.value;
      const spFire = this.props.spFire.value;
      if (card == null) {
        return;
      }
      img.texture = App.texture(`cards/${card.meta.name}.png`);
      grid.update({ card });
      szMeter.text = `${card.meta.spaceCount}`;
      spMeter.update({
        matrix: {
          spaces: Array(card.meta.specialCost).fill(true),
          width: 5,
          height: 2,
        },
      });

      this.lock(cardRoot);
      // TODO: support spFire
      // this.spMeter.forEach((space, i) => {
      //   const v = i < card.meta.specialCost ? Spaces.SPECIAL : Spaces.INVALID;
      //   space.update({
      //     value: v,
      //     spFire: spFire && Math.abs(v) != Spaces.INVALID,
      //   });
      // });
    };
    this.props.card.onUpdate(fn);
    this.props.spFire.onUpdate(fn);

    this.interactions = CardInteractions.install(this, { radius });
  }
}
