import { Texture } from "pixi.js";
import { Color } from "./pixi/Color";
import { Component } from "./pixi/Component";
import { EaseFunc } from "./pixi/animations/Ease";
import { WheelHandler } from "./pixi/events/WheelHandler";
import { TargetAnimation } from "./pixi/animations/TargetAnimation";
import { DragHandler } from "./pixi/events/DragHandler";

interface IItemListComponentOptions {
  width: number;
  height: number;
}

interface IItemListComponentProps<T> {
  items: T[];
  bg: {
    color: Color;
    alpha: number;
  };
  layout: {
    xlimit: number;
    padding?: {
      x: number;
      y?: number;
    };
    anchor?: {
      x: number;
      y?: number;
    };
  };
}

export class ItemListComponent<
  T extends Component = Component
> extends Component<IItemListComponentProps<T>> {
  private items: T[] = [];

  private height: number = 0;

  constructor(options: IItemListComponentOptions) {
    super({
      items: [],
      bg: {
        color: Color.WHITE,
        alpha: 0.5,
      },
      layout: {
        xlimit: 4,
      },
    });

    const { width, height } = options;
    this.layout = { width, height };

    const bg = this.addSprite({
      width,
      height,
      texture: Texture.WHITE,
    });

    const root = this.addContainer();
    root.mask = this.addSprite({
      width,
      height,
      texture: Texture.WHITE,
    });

    const itemRoot = this.addContainer({
      parent: root,
    });
    this.props.items.onUpdate((items) => {
      itemRoot.removeChildren();
      this.items = items.slice();
      if (items.length != 0) {
        const { width, height } = items[0].layout;
        console.assert(
          items.every(
            ({ layout: { width: w, height: h } }) => width == w && height == h
          )
        );
        items.forEach((item) => itemRoot.addChild(item.ui));
        this.updateLayout();
      }
    });

    this.props.layout.onUpdate(() => {
      if (this.items.length != 0) {
        this.updateLayout();
      }
    });

    this.props.bg.onUpdate(({ color, alpha }) => {
      bg.tint = color.i32;
      bg.alpha = alpha;
    });

    const scroll = TargetAnimation.of(0)
      .onEase(0.4, EaseFunc.EASE_IN_OUT_CUBIC)
      .onUpdate((v) => (itemRoot.y = v));

    const self = this;
    this.handle(
      class extends WheelHandler {
        stops: EventType[] = ["wheel"];
        wheel(pos: Coordinate, dy: number) {
          dy *= 3;
          const h = self.height - self.layout.height;
          const y = Math.min(0, Math.max(-h, scroll.targetValue - dy));
          scroll.update(y);
        }
      }
    );
    this.handle(
      class extends DragHandler {
        pos0: Coordinate;

        drag({ x, y }: Coordinate): void {
          this.pos0 = { x, y };
          console.log("drag", this.pos0);
        }
        move(pos: Coordinate): void {
          console.log("move", pos);
        }
        drop(pos: Coordinate): void {
          console.log("drop", pos);
        }
      }
    );
  }

  private updateLayout() {
    const {
      xlimit,
      padding = { x: 0 },
      anchor = { x: 0 },
    } = this.props.layout.value;
    const { x: dx, y: dy = dx } = padding;
    const { x: ax, y: ay = ax } = anchor;

    const { width: w, height: h } = this.items[0].layout;
    const w1 = (this.layout.width - (xlimit + 1) * dx) / xlimit;
    const e = w1 / w;
    const h1 = h * e;

    const ny = Math.ceil(this.items.length / xlimit);
    this.height = ny * h1 + (ny + 1) * dy;

    this.items.forEach((item) => item.scaleToFit(w1, h1));
    let y = 0;
    for (; ; ++y) {
      for (let x = 0; x < xlimit; ++x) {
        const i = x + y * xlimit;
        if (i >= this.items.length) {
          return;
        }
        const item = this.items[i];
        item.position.set(
          (w1 + dx) * x + dx + w1 * ax,
          (h1 + dy) * y + dy + h1 * ay
        );
        item.anchor.set(ax, ay);
      }
    }
  }
}
