import { ButtonComponent } from "../2d/ButtonComponent";
import { ColorPalette } from "../2d/ColorPalette";
import { PointerHandler } from "../2d/pixi/events/PointerHandler";
import { PixiScene } from "../2d/pixi/Scene";

export class EntryScene extends PixiScene {
  layout = {
    width: 1920,
    height: 1080,
  };

  constructor() {
    super({
      bgTint: ColorPalette.Main.bg.primary,
    });

    const self = this;

    const gamePlayBtn = this.addComponent(new ButtonComponent({}), {
      x: 400,
      y: 480,
    }).update({
      text: "gamePlayBtn",
    });
    gamePlayBtn.handle(
      class extends PointerHandler {
        tap(pos: Coordinate): void {
          self.send("action", "gamePlay");
        }
      }
    );

    const deckEditBtn = this.addComponent(new ButtonComponent({}), {
      x: 800,
      y: 480,
    }).update({
      text: "deckEditBtn",
    });
    deckEditBtn.handle(
      class extends PointerHandler {
        tap(pos: Coordinate): void {
          self.send("action", "deckEdit");
        }
      }
    );
  }
}
