import { Color } from "./pixi/Color";
import { Component } from "./pixi/Component";
import { EaseFunc } from "./pixi/animations/Ease";
import { PointerHandler } from "./pixi/events/PointerHandler";
import { TargetAnimation } from "./pixi/animations/TargetAnimation";

interface IButtonComponentOptions {
  width?: number;
  height?: number;
  radius?: number;
}

interface IButtonComponentProps {
  color: Color;
  text: string;
}

export class ButtonComponent extends Component<IButtonComponentProps> {
  constructor(options?: IButtonComponentOptions) {
    super({
      color: Color.BLACK,
      text: "btn",
    });

    const { width = 300, height = 100, radius = 0 } = options || {};
    this.layout = { width, height };

    const bg = this.addGraphics()
      .beginFill(Color.WHITE.i32)
      .drawRoundedRect(0, 0, width, height, radius);

    const dx = 2,
      dy = 2;
    const text1 = this.addText({
      anchor: 0.5,
      x: width / 2 + dx,
      y: height / 2 + dy,
      style: {
        fontSize: height / 3,
        fontFamily: "Splatoon",
        fill: Color.BLACK.i32,
      },
    });
    const text = this.addText({
      anchor: 0.5,
      x: width / 2,
      y: height / 2,
      style: {
        fontSize: height / 3,
        fontFamily: "Splatoon",
        fill: Color.WHITE.i32,
      },
    });

    const overlay = this.addGraphics()
      .beginFill(Color.WHITE.i32)
      .drawRoundedRect(0, 0, width, height, radius);
    overlay.alpha = 0;

    this.anchor.set(0.5);

    const scale = TargetAnimation.of(1)
      .onEase(0.1, EaseFunc.EASE_IN_OUT_CUBIC)
      .onUpdate((v) => this.scale.set(v));

    const alpha = TargetAnimation.of(0)
      .onEase(0.1, EaseFunc.EASE_IN_OUT_CUBIC)
      .onUpdate((v) => (overlay.alpha = v));

    this.handle(
      class extends PointerHandler {
        enter(pos: Coordinate): void {
          scale.update(1.03);
          alpha.update(0.2);
        }

        leave(pos: Coordinate): void {
          scale.update(1);
          alpha.update(0);
        }
      }
    );

    this.props.color.onUpdate((v) => (bg.tint = v.i32));
    this.props.text.onUpdate((v) => (text1.text = text.text = v));
  }
}
