import { Card } from "./Card";
import MiniGamePresetDeck from "./MiniGamePresetDeck.json";
import { shuffle } from "./Utils";

export class Deck {
  constructor(public readonly cards: Card[]) {
    console.assert(cards.length == 15);
    cards.forEach((card) => {
      console.assert(card != null);
    });
  }

  shuffle(): Deck {
    return new Deck(shuffle(this.cards));
  }

  static fromCardIds(cardIds: number[]): Deck {
    return new Deck(cardIds.map((id) => Card.fromId(id)));
  }

  static fromPreset(name: string): Deck {
    return Deck.NAME_LOOKUP.get(name);
  }

  private static readonly NAME_LOOKUP = new Map();

  static {
    for (const deck of MiniGamePresetDeck) {
      const cards = [];
      for (const card of deck["Card"]) {
        const name = card.match(/MiniGame_(\w+)\.spl/)[1];
        cards.push(Card.fromName(name));
      }
      const deckName = deck["__RowId"].match(/MiniGame_(\w+)/)[1];
      Deck.NAME_LOOKUP.set(deckName, new Deck(cards));
    }
  }
}
