import { Spaces } from "./Common";
import MiniGameCardInfo from "./MiniGameCardInfo.json";

interface CardMeta {
  id: number;
  name: string;
  rarity: "Common" | "Rare" | "Fresh";
  specialCost: number;
  spaceCount: number;
}

export class Card {
  constructor(
    public readonly spaces: number[],
    public readonly meta?: CardMeta
  ) {
    // basic data verifications
    console.assert(spaces.length == 8 * 8);
    console.assert(spaces.filter((x) => x < 0).length == 0);
    if (meta != null) {
      console.assert(spaces.filter((x) => x > 0).length == meta.spaceCount);
    }
  }

  get(pos: Coordinate): number {
    return this.spaces[pos.x + pos.y * 8];
  }

  rotate(rotation: Rotation): Card {
    const [a, b, c, d, e, f] = Card.ROTATION_MATRICES[rotation];
    const spaces = [];
    for (let y = 0; y < 8; ++y) {
      for (let x = 0; x < 8; ++x) {
        const pos = { x: a * x + b * y + c, y: d * x + e * y + f };
        spaces.push(this.get(pos));
      }
    }
    return new Card(spaces);
  }

  forEach(func: (x: number, y: number, v: number) => void) {
    for (let y = 0; y < 8; ++y) {
      for (let x = 0; x < 8; ++x) {
        const v = this.get({ x, y });
        if (v > 0) {
          func(x, y, v);
        }
      }
    }
  }

  static fromId(id: number): Card {
    return Card.ID_LOOKUP.get(id);
  }

  static fromName(name: string): Card {
    return Card.NAME_LOOKUP.get(name);
  }

  private static readonly ID_LOOKUP = new Map();
  private static readonly NAME_LOOKUP = new Map();
  private static readonly ROTATION_MATRICES = [
    [1, 0, 0, 0, 1, 0],
    [0, 1, 0, -1, 0, 7],
    [-1, 0, 7, 0, -1, 7],
    [0, -1, 7, 1, 0, 0],
  ];

  static {
    for (let info of MiniGameCardInfo) {
      const id = info["Number"];
      const name = info["Name"];
      const spaces = [];
      for (let y = 0; y < 8; ++y) {
        for (let x = 0; x < 8; ++x) {
          const v0 = info["Square"][x + (7 - y) * 8];
          let v = Spaces.EMPTY;
          if (v0 == "Fill") {
            v = Spaces.TRIVIAL;
          } else if (v0 == "Special") {
            v = Spaces.SPECIAL;
          }
          spaces.push(v);
        }
      }
      const card = new Card(spaces, {
        id,
        name,
        rarity: <any>info["Rarity"],
        specialCost: info["SpecialCost"],
        spaceCount: spaces.filter((x) => x > 0).length,
      });
      Card.ID_LOOKUP.set(id, card);
      Card.NAME_LOOKUP.set(name, card);
    }
  }
}
