import { Board } from "./Board";
import { Deck } from "./Deck";
import { GameState } from "./GameState";

type OnNewStateCallback = (from: GameState, to: GameState) => void;

export class GameController {
  private onNewStateCallback: OnNewStateCallback;

  constructor(
    public readonly states: GameState[],
    private stateId: number = 0
  ) {
    console.assert(stateId < states.length);
    this.onNewState();
  }

  forward(): GameState {
    console.assert(this.stateId < this.states.length);
    const from = this.states[this.stateId];
    const to = this.states[++this.stateId];
    this.onNewStateCallback(from, to);
    return this.state;
  }

  backward(): GameState {
    console.assert(this.stateId > 0);
    const from = this.states[this.stateId];
    const to = this.states[--this.stateId];
    this.onNewStateCallback(from, to);
    return this.state;
  }

  move(m1: PlayerMovement, m2: PlayerMovement): GameState {
    const from = this.states[this.stateId];
    const to = from.move(m1, m2);
    this.states.splice(++this.stateId);
    this.states.push(to);
    this.onNewStateCallback(from, to);
    return this.state;
  }

  onNewState(callback?: OnNewStateCallback) {
    this.onNewStateCallback = callback || (() => {});
  }

  get state(): GameState {
    return this.states[this.stateId];
  }

  static fromGameRecord(record: GameRecord): GameController {
    const board = Board.fromId(record.board);
    const [deck1, deck2] = record.decks.map(Deck.fromCardIds);
    let state = GameState.initial(board, deck1, deck2);
    const states = [state];
    for (const [m1, m2] of record.moves || []) {
      console.assert(state.isValidMovement(1, m1));
      console.assert(state.isValidMovement(-1, m2));
      state = state.move(m1, m2);
      states.push(state);
    }
    return new GameController(states);
  }
}
