import { Card } from "../core/Card";
import { App } from "./2d/pixi/App";
import { EntryScene } from "./scripts/EntryScene";
import { GamePlayScene } from "./scripts/GamePlayScene";
import { InkResetAnimation } from "./scripts/InkResetAnimation";
import { DeckEditScene } from "./scripts/DeckEditScene";
import { shuffle } from "../core/Utils";
import { TestScene } from "./scripts/TestScene";

export async function main() {
  const inkReset = new InkResetAnimation();
  const game = new GamePlayScene();
  const deckEdit = new DeckEditScene();

  // const test = new TestScene();
  // App.run(test);
  // return;

  let deck = [33, 159, 92, 25, 30, 52, 65, 50, 66, 64, 53, 58, 28, 74, 69];

  const entry = new EntryScene();
  App.run(entry);

  const loopFn = async () => {
    const action = await entry.receive("action");
    switch (action) {
      case "gamePlay":
        {
          App.wait(inkReset, async () => {
            game.reset({
              board: 6,
              decks: [
                shuffle(deck),
                [
                  86, 110, 105, 161, 53, 118, 125, 69, 109, 20, 143, 44, 12,
                  113, 75,
                ],
              ],
            });
            App.run(game);
          });
          // await
        }
        break;
      case "deckEdit":
        {
          App.wait(inkReset, async () => {
            deckEdit.reset(deck);
            App.run(deckEdit);
          });
          const { ok, deck: newDeck } = await deckEdit.receive("action");
          if (ok && newDeck.length == 15) {
            deck = newDeck.slice();
          }
          App.wait(inkReset, async () => {
            App.run(entry);
          });
        }
        break;
    }
  };

  while (true) {
    await loopFn();
  }
}
