import { RenderTexture, Texture } from "pixi.js";
import { App } from "./App";
import { Component } from "./Component";

// TODO: make this always valid
export class RenderBuffer<T = {}> extends Component<T> {
  private mTexture: Texture;

  constructor(props: T, private readonly animate: boolean = true) {
    super(props);
  }

  get texture(): Texture {
    if (this.mTexture != null) {
      return this.mTexture;
    }

    const renderTexture = RenderTexture.create({
      width: this.layout.width,
      height: this.layout.height,
    });
    const fn = () => {
      App.renderer.render(this.ui, { renderTexture });
      if (this.animate) {
        window.requestAnimationFrame(fn);
      }
    };
    window.requestAnimationFrame(fn);

    this.mTexture = renderTexture;
    return this.mTexture;
  }
}
