import { Board, CardPlacement } from "../../core/Board";
import { Card } from "../../core/Card";
import { Component } from "./pixi/Component";
import { GridComponent } from "./GridComponent";
import { Container, InteractionEvent, Sprite, Texture } from "pixi.js";
import { Spaces } from "../../core/Common";
import { EmptySpaceTexture } from "./EmptySpaceTexture";
import { SpFireTexture } from "./SpFireTexture";
import { OverlayTexture } from "./OverlayTexture";
import { Animation } from "./pixi/animations/Animation";
import { EaseFunc } from "./pixi/animations/Ease";
import { PointerHandler } from "./pixi/events/PointerHandler";
import { TargetAnimation } from "./pixi/animations/TargetAnimation";
import { getHitPos } from "./pixi/events/Utils";

interface IPlayerInput {
  card: Card;
  rotation: Rotation;
  pointer: Coordinate;
  isSpecialAttack: boolean;
}

interface IBoardComponentProps {
  input: IPlayerInput;
  acceptInput: boolean;
}

type OnPlayerInputFn = (e: CardPlacement, ok: boolean) => void;

export class BoardComponent extends Component<IBoardComponentProps> {
  private readonly root: Container;
  private readonly hitbox: Sprite;
  private readonly bg: GridComponent<number>;
  private readonly spaces: GridComponent<number>;
  private readonly spFire: GridComponent<number>;
  private readonly flash: GridComponent<boolean>;
  private readonly overlay: GridComponent<number>;

  private readonly nrmOverlay = OverlayTexture.normal();
  private readonly spOverlay = OverlayTexture.special();

  private readonly flashAnimation: Animation;

  private board: Board;
  private lk: boolean = false;
  private selection: number[];

  private onPlayerInputFn: OnPlayerInputFn = () => {};

  layout = {
    width: 20,
    height: 27,
    w1: 40,
  };

  constructor() {
    super({
      input: {
        card: null,
        rotation: 0,
        pointer: null,
        isSpecialAttack: false,
      },
      acceptInput: true,
    });

    const { w1 } = this.layout;

    this.root = this.addContainer();
    this.root.scale.set(1 / w1);

    this.hitbox = this.addSprite({ parent: this.root });

    this.bg = this.addComponent(new GridComponent(), {
      parent: this.root,
    });
    this.bg.update({
      tileset: new Map<any, any>([
        [Spaces.EMPTY, EmptySpaceTexture.V],
        [Spaces.TRIVIAL, "pure_black.png"],
        [-Spaces.TRIVIAL, "pure_black.png"],
        [-Spaces.TRIVIAL, "pure_black.png"],
        [Spaces.NEUTRAL, "pure_black.png"],
      ]),
      transform: {
        anchor: 0.5,
      },
    });

    const e1 = { texture: "player1_trivial_space.png", alpha: 1 };
    const e2 = { texture: "player2_trivial_space.png", alpha: 1 };
    const tileset = new Map<any, any>([
      [Spaces.TRIVIAL, e1],
      [Spaces.SPECIAL, "player1_special_space.png"],
      [-Spaces.TRIVIAL, e2],
      [-Spaces.SPECIAL, "player2_special_space.png"],
      [Spaces.NEUTRAL, "neutral_space.png"],
    ]);

    const tileAlpha = TargetAnimation.of(1)
      .onEase(0.2, EaseFunc.EASE_IN_OUT_EXPO)
      .onUpdate((v) => {
        this.spaces.update({ matrix: this.spaces.props.matrix.value });
        e1.alpha = e2.alpha = v;
      });

    this.spaces = this.addComponent(new GridComponent(), {
      parent: this.root,
    });
    this.spaces.update({
      tileset,
      transform: {
        anchor: 0.5,
      },
    });

    this.spFire = this.addComponent(new GridComponent(), {
      parent: this.root,
      y: -16,
    });
    this.spFire.update({
      tileset: new Map([
        [Spaces.SPECIAL, SpFireTexture.P1],
        [-Spaces.SPECIAL, SpFireTexture.P2],
      ]),
      transform: {
        anchor: 0.5,
        dx: w1,
        dy: w1,
      },
    });

    this.overlay = this.addComponent(new GridComponent(), {
      parent: this.root,
    });
    this.overlay.update({
      tileset: new Map([
        [Spaces.TRIVIAL, this.nrmOverlay.texture],
        [Spaces.SPECIAL, this.spOverlay.texture],
      ]),
      transform: {
        anchor: 0.5,
      },
    });

    this.flash = this.addComponent(new GridComponent(), {
      parent: this.root,
    });
    this.flash.update({
      tileset: new Map([[true, "Decide.png"]]),
      transform: {
        anchor: 0.5,
        dx: w1,
        dy: w1,
      },
    });

    this.flashAnimation = this.addAnimation({
      time: 0.2,
      keyframe: (t) => {
        // TODO: support grid tint
        // this.flash.tint = e.interpolate(
        //   ColorPalette.Space.flash.secondary,
        //   ColorPalette.Space.flash.primary,
        //   t
        //   ).i32;
        const scale = EaseFunc.LINEAR.interpolate(1, 1.5, t);
        const alpha =
          0.5 * EaseFunc.EASE_IN_OUT_SINE.apply(1 - Math.abs(2 * t - 1));
        this.flash.update({
          transform: Object.assign({}, this.flash.props.transform.value, {
            scale,
            alpha,
          }),
        });
      },
    });

    const self = this;
    this.handle(
      class extends PointerHandler {
        private getGridPos(pos: Coordinate): Coordinate {
          pos = getHitPos(pos, self.hitbox);
          if (pos == null) {
            return;
          }
          let { x, y } = pos;
          const { width: w, height: h } = self.board;
          return { x: Math.floor(x * w), y: Math.floor(y * h) };
        }

        move(pos: Coordinate): void {
          if (!self.props.acceptInput.value) {
            return;
          }
          pos = this.getGridPos(pos);
          if (pos == null || self.lk) {
            return;
          }
          self.update({
            input: Object.assign({}, self.props.input.value, { pointer: pos }),
          });
        }

        async tap(pos: Coordinate) {
          if (!self.props.acceptInput.value) {
            return;
          }
          pos = this.getGridPos(pos);
          if (pos == null) {
            return;
          }
          const move = self.uiUpdateInput(
            Object.assign({}, self.props.input.value, { pointer: pos })
          );
          if (move == null) {
            return;
          }
          self.send("player.input", move);
          self.lk = true;
          self.flash.update({
            matrix: {
              spaces: self.selection.map((x) => x != Spaces.EMPTY),
              width: self.board.width,
              height: self.board.height,
            },
          });
          await self.flashAnimation.play();
          self.lk = false;
          self.send("player.input", move);
        }
      }
    );

    const fn = () => {
      const input = this.props.input.value;
      const accept = this.props.acceptInput.value;
      if (accept && input) {
        tileAlpha.update(input.isSpecialAttack ? 0.4 : 1);
        this.uiUpdateInput(input);
      } else {
        tileAlpha.update(1);
        this.uiUpdateInput(null);
      }
    };
    this.props.input.onUpdate(fn);
    this.props.acceptInput.onUpdate(fn);
  }

  uiReset(board: Board) {
    this.board = board;
    const { width, height } = this.board;
    const x = (this.layout.width - width) / 2;
    const y = (this.layout.height - height) / 2;
    this.root.position.set(x + 0.5, y + 0.5);
    const { w1 } = this.layout;
    this.hitbox.width = w1 * width;
    this.hitbox.height = w1 * height;
    this.spaces.update({ matrix: board });
    this.bg.update({ matrix: board });
    this.bg.lock();
    this.uiUpdateFire();
  }

  uiUpdateFire() {
    const spaces: number[] = [];
    const { width, height } = this.board;
    for (let y = 0; y < height; ++y) {
      for (let x = 0; x < width; ++x) {
        const pos = { x, y };
        spaces.push(this.board.isCharged(pos) ? this.board.get(pos) : 0);
      }
    }
    this.spFire.update({
      matrix: { spaces, width, height },
    });
  }

  async uiPlaceCards(ms: CardPlacement[]) {
    console.assert(ms.length <= 2);
    if (ms.length == 2) {
      console.assert(ms[0].turn == -ms[1].turn);
    }
    this.board = this.board.move(ms);
    this.spaces.update({ matrix: this.board });
    this.bg.update({ matrix: this.board });
    this.bg.lock();
    const { width, height } = this.board;
    const spaces: boolean[] = [];
    for (const m of ms) {
      m.card.rotate(m.rotation).forEach((x, y) => {
        x += m.position.x;
        y += m.position.y;
        spaces[x + y * width] = true;
      });
    }
    this.flash.update({ matrix: { spaces, width, height } });
    await this.flashAnimation.play();
  }

  onPlayerInput(fn: OnPlayerInputFn) {
    this.onPlayerInputFn = fn;
  }

  private uiUpdateInput(input?: IPlayerInput): CardPlacement {
    this.selection = [];
    if (this.board == null) {
      return null;
    }

    const { width, height } = this.board;
    this.overlay.update({
      matrix: { spaces: this.selection.slice(), width, height },
    });
    this.overlay.lock();
    if (input == null) {
      return null;
    }

    const { card, rotation, pointer, isSpecialAttack } = input;
    if (
      card == null ||
      rotation == null ||
      pointer == null ||
      isSpecialAttack == null
    ) {
      return null;
    }

    const position = this.getAdjustedCardPosition(card, rotation, pointer);
    const move: CardPlacement = {
      turn: 1,
      card,
      rotation,
      position,
    };

    const ok = this.board.isValidMovement(isSpecialAttack, move);
    const value = ok ? 1 : 0;
    this.nrmOverlay.update({ value });
    this.spOverlay.update({ value });
    card.rotate(rotation).forEach((x, y, v) => {
      x += position.x;
      y += position.y;
      this.selection[x + width * y] = v;
    });
    this.overlay.update({
      matrix: { spaces: this.selection.slice(), width, height },
    });
    this.overlay.lock();

    this.onPlayerInputFn(move, ok);

    return move;
  }

  private getAdjustedCardPosition(
    card: Card,
    rotation: Rotation,
    pointer: Coordinate
  ): Coordinate {
    let x = pointer.x - 3;
    let y = pointer.y - 3;

    if (rotation == 1 || rotation == 2) {
      x -= 1;
    }
    if (rotation == 2 || rotation == 3) {
      y -= 1;
    }

    const rotatedCard = card.rotate(rotation);
    const xs: number[] = [];
    const ys: number[] = [];
    rotatedCard.forEach((dx, dy, _) => {
      xs.push(x + dx);
      ys.push(y + dy);
    });

    const minX = Math.min(...xs);
    const maxX = Math.max(...xs);
    const minY = Math.min(...ys);
    const maxY = Math.max(...ys);
    if (minX < 0) {
      x += -minX;
    }
    if (minY < 0) {
      y += -minY;
    }
    if (maxX >= this.board.width) {
      x -= maxX - this.board.width + 1;
    }
    if (maxY >= this.board.height) {
      y -= maxY - this.board.height + 1;
    }

    return { x, y };
  }
}
