import { Card } from "../../core/Card";
import { ButtonComponent } from "../2d/ButtonComponent";
import { CardComponent } from "../2d/CardComponent";
import { SmallCardComponent } from "../2d/SmallCardComponent";
import { ItemListComponent } from "../2d/ItemListComponent";
import { Color } from "../2d/pixi/Color";
import { PointerHandler } from "../2d/pixi/events/PointerHandler";
import { PixiScene } from "../2d/pixi/Scene";
import { Logger } from "../2d/pixi/Logger";

export class DeckEditScene extends PixiScene {
  layout = {
    width: 1920,
    height: 1080,
  };

  private readonly cardIdMap = new Map<number, CardComponent>();
  private readonly cards: SmallCardComponent[] = [];
  private readonly deckView: ItemListComponent<SmallCardComponent>;

  constructor() {
    super({
      bgTint: Color.fromHex(0x333333),
    });

    const self = this;
    for (let i = 0; i < 15; ++i) {
      const card = new SmallCardComponent().update({ card: Card.fromId(1) });
      card.interactions.onTap(() => {
        const items = this.deckView.props.items.value.slice();
        const idx = items.findIndex((e) => e == card);
        Logger.assert(idx != -1);
        items.splice(idx, 1);
        const { id } = card.props.card.value.meta;
        this.cardIdMap.get(id).interactions.disabled.update(false);
        this.deckView.update({ items });
      });
      this.cards.push(card);
    }

    // init card collection
    for (let i = 1; i <= 162; ++i) {
      const card = new CardComponent();
      card.update({ card: Card.fromId(i) });
      card.interactions.onTap(() => {
        const items = this.deckView.props.items.value.slice();
        if (items.length >= 15) {
          return;
        }
        const { id } = card.props.card.value.meta;
        const idx = items.findIndex(
          (card) => card.props.card.value.meta.id == id
        );
        Logger.assert(idx == -1);
        for (const card of this.cards) {
          if (!items.includes(card)) {
            card.update({ card: Card.fromId(id) });
            items.push(card);
            this.deckView.update({ items });
            this.cardIdMap.get(id).interactions.disabled.update(true);
            return;
          }
        }
        Logger.unreachable();
      });
      this.cardIdMap.set(i, card);
    }

    // init collection view
    this.addComponent(
      new ItemListComponent({
        width: 1460,
        height: 1080,
      })
    ).update({
      items: Array.from(this.cardIdMap.values()),
      layout: {
        xlimit: 7,
        padding: {
          x: 20,
        },
        anchor: {
          x: 0.5,
        },
      },
    });

    // init deck view
    this.deckView = this.addComponent(
      new ItemListComponent<SmallCardComponent>({
        width: 460,
        height: 1080,
      }),
      {
        x: 1460,
      }
    ).update({
      bg: {
        color: Color.fromHex(0x101010),
        alpha: 1,
      },
      layout: {
        xlimit: 3,
        padding: {
          x: 10,
        },
        anchor: {
          x: 0.5,
        },
      },
    });

    this.addComponent(
      new ButtonComponent({
        width: 200,
        height: 80,
      }),
      {
        x: 1800,
        y: 1020,
      }
    )
      .update({
        text: "okBtn",
      })
      .handle(
        class extends PointerHandler {
          tap(pos: Coordinate): void {
            self.send("action", {
              ok: true,
              deck: self.deckView.props.items.value.map(
                (e) => e.props.card.value.meta.id
              ),
            });
          }
        }
      );

    this.addComponent(
      new ButtonComponent({
        width: 200,
        height: 80,
      }),
      {
        x: 1580,
        y: 1020,
      }
    )
      .update({
        text: "cancelBtn",
      })
      .handle(
        class extends PointerHandler {
          tap(pos: Coordinate): void {
            self.send("action", {
              ok: false,
            });
          }
        }
      );
  }

  reset(deck: number[]) {
    this.cardIdMap.forEach((card, id) => {
      card.interactions.disabled.update(deck.includes(id));
    });
    this.deckView.update({
      items: deck.map((id, i) =>
        this.cards[i].update({ card: Card.fromId(id) })
      ),
    });
  }
}
