import { Texture } from "pixi.js";
import { Color } from "./pixi/Color";
import { Component } from "./pixi/Component";
import BlendGlsl from "../shaders/Blend.glsl";
import { EaseFunc } from "./pixi/animations/Ease";

interface ITimerComponentProps {
  value: number;
}

export class TurnMeterComponent extends Component<ITimerComponentProps> {
  layout = {
    width: 0,
    height: 0,
  };

  private readonly updateFn: (v: number) => Promise<void>;

  constructor() {
    super({
      value: 0,
    });

    const bgShader = this.addShader(BlendGlsl, {
      uColorPrimary: Color.fromHex(0x838286).rgb01,
      uColorSecondary: Color.fromHex(0x737173).rgb01,
    });

    const root = this.addContainer();

    const shadow = this.addGraphics({ parent: root, x: 4, y: 4 })
      .beginFill(Color.BLACK.i32)
      .drawCircle(0, 0, 90);
    shadow.alpha = 0.2;

    const bgRoot = this.addContainer({
      parent: root,
      filters: [bgShader],
    });

    this.addGraphics({ parent: bgRoot })
      .beginFill(Color.WHITE.i32)
      .drawCircle(0, 0, 90);

    this.addSprite({
      parent: bgRoot,
      anchor: 0.5,
      x: -5.8,
      y: 20,
      width: 340 * 0.5,
      height: 114 * 0.5,
      angle: -6,
      texture: "GftImage_44.png",
    });

    this.addSprite({
      parent: bgRoot,
      anchor: 0.5,
      x: -18,
      y: 41.6,
      width: 400 * 0.3,
      height: 288 * 0.3,
      tint: Color.fromHex(0x080808),
      texture: "GftVsResult_02.png",
    });

    this.addSprite({
      parent: bgRoot,
      anchor: 0.5,
      x: 19,
      y: -34,
      angle: -165.5,
      width: 202 * 0.9,
      height: 64 * 0.9,
      texture: "GftImage_32.png",
    });

    const title = this.addText({
      parent: root,
      anchor: 0.5,
      x: 0,
      y: -54,
      text: "Turns Left",
      style: {
        fill: Color.fromHex(0xd0d0d0).i32,
        fontFamily: "Splatoon",
        fontSize: 28,
      },
    });
    this.castShadow(title, { x: 1, y: 1, alpha: 0.2 });

    const counter = this.addText({
      parent: root,
      anchor: 0.5,
      x: 0,
      y: 0,
      text: "0",
      style: {
        fontFamily: "Splatoon",
        fontSize: 76,
      },
    });
    this.castShadow(counter, { x: 5, y: 5, alpha: 0.2 });

    this.props.value.onUpdate((v) => {
      counter.style.fill =
        v <= 3 ? Color.fromHex(0xf04833).i32 : Color.WHITE.i32;
      counter.text = `${v}`;
    });

    this.updateFn = async (v) => {
      const h = 20;
      const dt = 0.05;
      const dt1 = 0.2;
      const dt2 = 0.4;
      const scale = 1.2;

      const a1 = this.addAnimation((t) => {
        counter.y = 0;
        root.scale.set(EaseFunc.LINEAR.interpolate(1, scale, t));
      });

      const a2 = this.addAnimation((t) => {
        const e = EaseFunc.EASE_OUT_CUBIC.apply(t);
        counter.y = EaseFunc.LINEAR.interpolate(0, -h, e);
      });

      const a3 = this.addAnimation((t) => {
        const e = EaseFunc.EASE_IN_CUBIC.apply(t);
        counter.y = EaseFunc.LINEAR.interpolate(-h, 0, e);
      });

      const a4 = this.addAnimation((t) => {
        counter.y = 0;
        root.scale.set(EaseFunc.LINEAR.interpolate(scale, 1, t));
      });

      const sleep = async (t: number) => await this.addAnimation().play(t);

      await a1.play(dt1);
      await sleep(dt2);

      await a2.play(dt);
      this.update({ value: v });
      await a3.play(dt);

      await sleep(dt2);
      await a4.play(dt1);
    };
  }

  async uiUpdate(value: number) {
    await this.updateFn(value);
  }
}
