import { Board, CardPlacement } from "./Board";
import { Card } from "./Card";
import { Deck } from "./Deck";

class PlayerState {
  constructor(
    public readonly deck: Card[],
    public readonly hand: Card[],
    public readonly specialPoints: number = 0,
    public readonly card?: Card,
    public readonly movement?: PlayerMovement
  ) {
    if (card != null) {
      console.assert(card.meta != null);
    }
  }

  move(m: PlayerMovement): PlayerState {
    const deck = this.deck.slice();
    const hand = this.hand.slice();
    console.assert(0 <= m.handId && m.handId < 4);
    let card = hand[m.handId];
    if (card == null) {
      console.assert(m.cardId != null);
      card = Card.fromId(m.cardId);
    }
    hand[m.handId] = deck.pop();
    let specialPoints = this.specialPoints;
    if (m.type == "special") {
      specialPoints -= card.meta.specialCost;
    }
    return new PlayerState(deck, hand, specialPoints, card, m);
  }

  static from(deck: Deck): PlayerState {
    const cards = deck.cards.slice().reverse();
    const hand = [];
    for (let i = 0; i < 4; ++i) {
      hand.push(cards.pop());
    }
    return new PlayerState(cards, hand);
  }
}

export class GameState {
  constructor(
    public readonly id: number,
    public readonly board: Board,
    public readonly player1: PlayerState,
    public readonly player2: PlayerState,
    public readonly cardPlacements?: CardPlacement[][]
  ) {
    console.assert(id >= 0);
  }

  move(m1: PlayerMovement, m2: PlayerMovement): GameState {
    if (!this.isValidMovement(1, m1) || !this.isValidMovement(-1, m2)) {
      throw `invalid movement: ${JSON.stringify([m1, m2])}`;
    }
    const { specialPoints1, specialPoints2 } = this.board;
    let earnSpecialPoints1 = 0;
    let earnSpecialPoints2 = 0;
    let board = this.board;
    let player1 = this.player1.move(m1);
    let player2 = this.player2.move(m2);
    let p1: CardPlacement;
    let p2: CardPlacement;
    let cardPlacements: CardPlacement[][] = [];
    if (m1.type != "discard") {
      p1 = {
        turn: 1,
        card: player1.card,
        rotation: m1.params.rotation,
        position: m1.params.position,
      };
    } else {
      earnSpecialPoints1 += 1;
    }
    if (m2.type != "discard") {
      p2 = {
        turn: -1,
        card: player2.card,
        rotation: m2.params.rotation,
        position: m2.params.position,
      };
    } else {
      earnSpecialPoints2 += 1;
    }
    if (p1 && p2) {
      let overlap = false;
      const li = new Set();
      const fn = (m: CardPlacement) => {
        m.card.rotate(m.rotation).forEach((x, y) => {
          const pos = { x: x + m.position.x, y: y + m.position.y };
          const key = pos.x.toString() + ":" + pos.y.toString();
          if (li.has(key)) {
            overlap = true;
          }
          li.add(key);
        });
      };
      [p1, p2].forEach(fn);

      const dn = player1.card.meta.spaceCount - player2.card.meta.spaceCount;
      if (!overlap || dn == 0) {
        cardPlacements = [[p1, p2]];
      } else if (dn < 0) {
        cardPlacements = [[p2], [p1]];
      } else {
        cardPlacements = [[p1], [p2]];
      }
    } else {
      if (p1) {
        cardPlacements = [[p1]];
      }
      if (p2) {
        cardPlacements = [[p2]];
      }
    }
    for (const li of cardPlacements) {
      board = board.move(li);
    }
    earnSpecialPoints1 += board.specialPoints1 - specialPoints1;
    earnSpecialPoints2 += board.specialPoints2 - specialPoints2;
    player1 = new PlayerState(
      player1.deck,
      player1.hand,
      player1.specialPoints + earnSpecialPoints1,
      player1.card,
      player1.movement
    );
    player2 = new PlayerState(
      player2.deck,
      player2.hand,
      player2.specialPoints + earnSpecialPoints2,
      player2.card,
      player2.movement
    );
    return new GameState(this.id + 1, board, player1, player2, cardPlacements);
  }

  isValidMovement(turn: Turn, m: PlayerMovement): boolean {
    if (m.handId < 0 || m.handId >= 4) {
      return false;
    }
    if (m.type == "discard") {
      return true;
    }
    const player = (turn > 0 ? this.player1 : this.player2).move(m);
    if (player.specialPoints < 0) {
      return false;
    }
    return this.board.isValidMovement(m.type == "special", {
      turn,
      card: player.card,
      rotation: m.params.rotation,
      position: m.params.position,
    });
  }

  static initial(board: Board, deck1: Deck, deck2: Deck): GameState {
    return new GameState(
      0,
      board,
      PlayerState.from(deck1),
      PlayerState.from(deck2)
    );
  }
}
